/// <reference path='../../index.ts' />

namespace alm.util {

	export class Arr {

		/**
		 * 
		 * @param {number} count 
		 * @param {number} init 
		 * @param {number} step 
		 * @returns {number[]} 
		 */
		public static sequence(count:number, init:number = 0, step:number = 1):number[] {
			const result:number[] = new Array(count);
			let v:number = init;
			for (let i:number = 0; i < count; ++i) {
				result[i] = v;
				v += step;
			}
			return result;
		}

		/**
		 * 
		 * @param list 
		 * @returns {T[]} 
		 */
		public static unique<T>(list:T[]):T[] {
			return list.filter(function (x:T, i:number, self:T[]) {
				return self.indexOf(x) === i;
			});
		}

		/**
		 * 
		 * @param list 
		 * @param unique true
		 * @returns {T[]} 
		 */
		public static duplicated<T>(list:T[], unique:boolean = false):T[] {

			if (unique) {
				return list.filter(function (x:T, i:number, self:T[]) {
					return self.indexOf(x) !== self.lastIndexOf(x);
				});
			} else {
				return list.filter(function (x:T, i:number, self:T[]) {
					return (self.indexOf(x) === i) && (self.lastIndexOf(x) !== i);
				});
			}
		}

		/**
		 * 2
		 * @param {T[]} list1 1
		 * @param {T[]} list2 2
		 * @param {(count: number, index1: number, index2: number, element1: T, element2: T) => void} callback 
		 * @param callback.count 
		 * @param callback.index1 1
		 * @param callback.index2 2
		 * @param callback.element1 1
		 * @param callback.element2 2
		 */
		public static roundRobin<T>(list1:T[], list2:T[], callback:(count:number, index1:number, index2:number, element1:T, element2:T) => void):void {
			let i:number, j:number, p:number = 1;
			const m:number = list1.length;
			const n:number = list2.length;
			for (i = 0; i < m; ++i) {
				for (j = i + 1; j < n; ++j) {
					callback(p++, i, j, list1[i], list2[j]);
				}
			}
		}

		private constructor() {}
	}
}
